<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>My Address</h1>
      <div class="chartWrap">
        <van-icon name="add-o" @click='redirect("/addAddress")' /></div>
    </section>
    <div class="center">
        <ul>
            <li v-for='(item,index) in addressList' :key="index">
                <div class="information">
                    <div class="tagging">
                        <span>{{item.lastName }}{{item.firstName}}</span>
                        <span class="number">NO.+971 {{item.mobile }}</span>
                        <span class="title" v-if="item.landmark">{{item.landmark }}</span>
                    </div>
                    <div class="content">
                            Address {{item.countryName}} {{item.provinceName}} {{item.cityName}} {{item.street}} {{item.lastName}} {{item.firstName}}
                    </div>
                </div>
                <div class="operation">
                    <span><a :class="['iconfont','cart',item.addressType==1?'yellow':'']" @click="changePrimary(item.id)"> &#xe65f; </a></span>
                    <span><a class="iconfont cart" @click='deleteAdd(item.id)'> &#xe609; </a></span>
                    <span><a class="iconfont cart" @click='redirect("/addAddress",{id:item.id})'> &#xe654; </a></span>
                </div>
            </li>

        </ul>
    </div>

</div>
</template>

<script>

import {myAddress,deleteAddress,editAddress} from '@/api/address';
export default {
  name: 'myAddress',
  data () {
    return {
        addressList:[]
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
       myAddress().then(res=>{
           this.addressList=res.data.result;
       })
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    showName(){
        this.control = true;
    },
    changePrimary(id){
         this.$dialog.confirm({
          message:'Tap other icon to change default address',
          confirmButtonText:'confirm' ,
          cancelButtonText:'cancel'
        }).then(()=>{
          editAddress({id:id,addressType:1}).then(res=>{
              this.$toast('Set as default successfully');
              this.init();
          })
        })
    },
    //删除地址
    deleteAdd(id){
        this.$dialog.confirm({
          message:'Are you sure to delete this address',
          confirmButtonText:'confirm' ,
          cancelButtonText:'cancel'
        }).then(()=>{
          deleteAddress(id).then(res=>{
              this.$toast('Delete Success');
              this.init();
          })
        })
    },

  }
}
</script>

<style lang="scss" scoped>
  .head{
    border-bottom:solid 1px #e5e5e5;
  }
   .center{
       width: 100%;
       padding-left: 4%;
       border-top: 10px solid #f6f6f6;
       .operation{
           width: 30%;
           display: flex; align-items: center; justify-content: space-around;
           padding: 0 5px;
           color: #666666;
       }
       .information{
           width: 70%;
           .content{
                font-size: 13px;
                color: #666666;
                width: 100%;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                word-break: break-all;
                padding-top: 10px;
            }
           .tagging{
                display: -webkit-box;
                width: 100%;
                span{
                    display: block;
                    font-size: 14px;
                    color: #333333;
                }
                .number{
                    margin: 0 10px 0 20px;
                }
                .title{
                    font-size: 10px !important;
                    color: #E33946;
                    border: 1px solid #E33946;
                    border-radius: 3px;
                    padding: 0 5px;
                    height: 15px;
                    line-height: 13px;
                }
           }

       }
       li{
        border-bottom: 1px solid #f6f6f6;
        display: -webkit-box;
        padding: 15px 0 10px 0;
   }
   }

   .yellow{
       color: #ffbb05;
   }
   .chartWrap{
       font-size: 21px;
       color: #e33946;
   }
</style>






